import React, { Component } from 'react';
import database from './Firebase';


class Projects extends Component {
  projectsList = [];
  extractData = data => {
    const evaData = data.val();
    const keys = Object.keys(evaData);
    const extractedData = keys.map(key => evaData[key]);
    return extractedData;
  };
  renderData = data => {
    const extractedData = this.extractData(data);
    this.projectsList = extractedData.map((entity, index) => {
      return (
        <div className="project" key={index}>
          <div className="project-title-container">
            <div>
              <div className="project-link-item">
                <svg className="project-icon" viewBox="0 0 1792 1792" xmlns="http://www.w3.org/2000/svg"><path d="M1520 1216q0-40-28-68l-208-208q-28-28-68-28-42 0-72 32 3 3 19 18.5t21.5 21.5 15 19 13 25.5 3.5 27.5q0 40-28 68t-68 28q-15 0-27.5-3.5t-25.5-13-19-15-21.5-21.5-18.5-19q-33 31-33 73 0 40 28 68l206 207q27 27 68 27 40 0 68-26l147-146q28-28 28-67zm-703-705q0-40-28-68l-206-207q-28-28-68-28-39 0-68 27l-147 146q-28 28-28 67 0 40 28 68l208 208q27 27 68 27 42 0 72-31-3-3-19-18.5t-21.5-21.5-15-19-13-25.5-3.5-27.5q0-40 28-68t68-28q15 0 27.5 3.5t25.5 13 19 15 21.5 21.5 18.5 19q33-31 33-73zm895 705q0 120-85 203l-147 146q-83 83-203 83-121 0-204-85l-206-207q-83-83-83-203 0-123 88-209l-88-88q-86 88-208 88-120 0-204-84l-208-208q-84-84-84-204t85-203l147-146q83-83 203-83 121 0 204 85l206 207q83 83 83 203 0 123-88 209l88 88q86-88 208-88 120 0 204 84l208 208q84 84 84 204z"></path></svg>
                <a href={entity.project_url} target="_blank" rel="noopener noreferrer"> <div className="project-name">{entity.project_name}</div></a>
                <div />
              </div>
            </div>
            <div className="project-date">
              {entity.project_date}
            </div>
          </div>
          <p className="project-p">{entity.project_description}</p>
        </div>
      )
    });
  }
  getData = () => database.on('value', this.renderData, this.onError);

  render() {
    this.getData();
    setTimeout(() => this.forceUpdate(), 1400);
    return (
      <section className="projects-container">
        <div className="project-section-title">Projects</div>
        <div>
          {this.projectsList}
        </div>
      </section>
    )
  }
}
export default Projects;
